<template>
		<div class="tradingRecord qrcode">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>把二维码发给好友，让好友一起加入会员行列吧！</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo" width="88%">
				</div>
				<p>转发此页面给好友，加入会员行列</p>
			</div>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	currUrl:'',
		    	urlCardNo:'',
		    	wxshareData:[],
		    	share:{
		    		urlShare:'',
			    	shareTitle:'您的好友给您推荐一张会员卡，赶紧领取吧!',
			    	shareDesc:'识别图中二维码领取会员卡',
			    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
		    	}
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.currUrl=window.location.href;
				_this.url=_this.currUrl.split('#');
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				alert(_this.currUrl)
				Lib.M.ajax({
			      	'url': memberAPI.activeAPI.wxshareUrl_POST,
			      	'type': 'POST',
			      	'data': {
			        	"json": json
			      	},
      				'success': function(data) {
      					alert(JSON.stringify(data))
      					_this.share.urlShare=data.data;
      					Lib.M.ajax({
					      	'url': memberAPI.activeAPI.wxshareCard_POST,
					      	'type': 'POST',
					      	'data': {
					        	"json": json
					      	},
		      				'success': function(data) {
		      					alert(JSON.stringify(data))
		      					_this.wxshareData=data.data;
		      				}
		      			})
      					
      				}
      			})
				
			}
		},
		created:function(){
	    	var _this=this;
	    },
	    mounted:function(){
	    	var _this=this;
	    	_this.loadData();
		},
		watch:{
			wxshareData(newValue, oldValue){
				var _this=this;
				alert(JSON.stringify(newValue))
				alert(JSON.stringify(_this.share))
				wx.config({
		            debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
		            appId: newValue.appid, // 必填，公众号的唯一标识
		            timestamp: newValue.timestamp, // 必填，生成签名的时间戳
		            nonceStr: newValue.nonce_str, // 必填，生成签名的随机串
		            signature: newValue.signature, // 必填，签名，见附录1
		            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
		        });
				wx.ready(function() {
		            //分享给朋友
		            wx.onMenuShareAppMessage({
			            title: _this.share.shareTitle, // 分享标题
			            desc: _this.share.shareDesc, // 分享描述
			            link: _this.share.urlShare, // 分享链接
			            imgUrl: _this.share.shareImg, // 分享图标
			            success: function() {
			                // 用户确认分享后执行的回调函数
			                Lib.M.tipsMsg('分享成功')
			            },
			            cancel: function() {
			                // 用户取消分享后执行的回调函数
			                 Lib.M.tipsMsg('取消分享')
			            },
			            error: function(res) {
			                alert(res)
			            }
		            });
		            //分享到朋友圈
		            wx.onMenuShareTimeline({
		             	title: _this.share.shareTitle, // 分享标题
		             	desc: _this.share.shareDesc, // 分享描述
		              	link: _this.share.urlShare, // 分享链接
		              	imgUrl: _this.share.shareImg, // 分享图标
		              	success: function() {
		                // 用户确认分享后执行的回调函数
		              	},
		              	cancel: function() {
		                // 用户取消分享后执行的回调函数
		              	}
		            });
		        });
			}
		}
	}
</script>
<style scope>

</style>


