<template>
	<transition enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutRight">
		<div class="AreaPop">
			<div  class="AreaPopCon">
				<!-- <span v-show="icon"><i class="icon iconfont icon-gou"></i></span>
				<p >{{titleCom}}</p>
				<p>{{titleComTow}}</p> -->
			</div>
		</div>
	</transition>
</template>
<script>
	export default{
		data() {
		    return {
			    
			}
		},
		// props:['titleCom','titleComTow','icon']
		props:{
			titleCom:'',
			titleComTow:'',
			icon:{
				default:1
			}
		}
	}
</script>
<style scope>
	/*弹窗样式*/
	.AreaPop{
		position: fixed;
		top: 0;
		left: 0;
		background: rgba(76,76,76,0.9);
		height: 100%;
    	width: 100%;
	}
	.AreaPopCon{
		width: 100%;
		font-size: 1rem;
		height: 100%
	}
	
</style>


